.case {
  margin-top: 80px;

  @include width-640 {
    margin-top: 60px;
  }
  &__title {
    max-width: 1000px;
    margin-bottom: 40px;
  }
  &__subtitle {
    display: block;
    font-weight: 500;
    font-size: 26px;
    line-height: 110%;
    color: rgba(0, 0, 0, 0.6);
    margin-bottom: 20px;
  }
  &-cards {
    margin-top: 160px;
    &__title {
      margin-bottom: 40px;
    }

    @include width-768 {
      margin-top: 120px;
    }
    @include width-640 {
      margin-top: 80px;
    }
  }
  &__body {
  }
}

.case-intro {
  background: #43CE6B;
  border: 3px solid #000000;
}

.case-text {
  display: grid;
  grid-template-columns: 610px 580px;
  gap: 80px;
  margin-bottom: 80px;
  margin-top: 160px;

  @include width-1450 {
    grid-template-columns: 520px 580px;
  }
  @include width-1250 {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  @include width-1250 {
    margin-top: 120px;
  }
  @include width-640 {
    margin-top: 80px;
    margin-bottom: 40px;
  }
  &__title {
  }

  &__disc {
    font-weight: 500;
    font-size: 18px;
    line-height: 130%;
    color: #000000;
    & p {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }

    @include width-1250 {
      max-width: 600px;
    }
    @include width-640 {
      @include scale(font-size,18,14);
    }
  }
}

.case-example {
  background: #EDEDED;
  padding: 100px 400px;

  @include width-1700 {
    padding: 100px 300px;
  }
  @include width-1450 {
    padding: 100px 200px;
  }
  @include width-1250 {
    padding: 60px 75px;
  }
  @include width-640 {
    @include scale(padding-left,75,40);
    @include scale(padding-right,75,40);
    @include scale(padding-top,60,40);
    @include scale(padding-bottom,60,40);
  }
  &__two {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
    padding: 100px 240px;

    @include width-1700 {
      padding: 100px 130px;
    }
    @include width-1250 {
      padding: 60px 40px;
      gap: 20px;
    }
    @include width-640 {
      grid-template-columns: 1fr;
      gap: 40px;
    }
  }
}


@keyframes rotateInf{
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.image-lazy {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.image-lazy::after {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  background: url('../img/pages/case/loading-icon.svg') no-repeat;
  background-size: 20px;
  animation: rotateInf 2s infinite linear;
  z-index: 0;
}

.image-lazy img {
  z-index: 1;
}
